@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Book.ttf);
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 600;
}

.form {
  margin-top: 20px;
}

.search {
  box-sizing: border-box;
  font-family: Avenir, sans-serif;
  font-weight: 300;
  width: 100%;
  background: url(images/Search.svg) no-repeat;
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
}

.search::placeholder {
  color: #3d4e61;
}

.search:focus {
  font-weight: 600;
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
}

.search:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.big_search {
  font-size: 16px;
  background-position: 26px;
  padding-top: 23px;
  padding-bottom: 23px;
  padding-left: 62px;
}

.small_search {
  font-size: 14px;
  background-position: 12px 14px;
  background-size: 11px 11px;
  padding-top: 10px;
  padding-bottom: 11px;
  padding-left: 29px;
}
